html {
  padding: 0px;
  margin: 0px;
  width: 100vw;
  height: 100vh;
}

.app {
  width: 100%;
  height: 100%;
}

textarea:focus {
  outline: none;
}

textarea {
  margin-top: 10px;
  border-radius: 10px;
  width: 100%;
  border: 0;
  transition: backgroud-color 1s;
  min-height: 300px;
  overflow: visible;
  box-sizing: border-box;
  padding: 10px 0;
  line-height: 40px;
  resize: none;
  font-size: xx-large;
  font-family: Candara, Calibri, Segoe, "Segoe UI", Optima, Arial, sans-serif;
  font-style: normal;
  font-variant: normal;
  background-image: linear-gradient(
    0deg,
    #666666 2.38%,
    transparent 2.38%,
    transparent 50%,
    #666666 50%,
    #666666 52.38%,
    transparent 52.38%,
    transparent 100%
  );
  background-size: 80px 80px;
}

.colorChoice {
  width: 2em;
  height: 2em;
  border-radius: 100%;
  margin-left: 10px;
  margin-right: 10px;
  border-width: 2px;
  border-style: solid;
  border-color: #b3b3b3;
  transition: 1s;
}
.colorChoice:hover {
  border-style: dotted;
  border-color: #808080;
  transform: rotate(30deg);
}

.pallete .info {
  width: 2em;
  height: 2em;
  border-radius: 10px;
  margin-left: 10px;
  margin-right: 10px;
  border-width: 2px;
  border-style: solid;
  border-color: #b3b3b3;
  transition: 1s;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: red;
  user-select: none;
  transition: 1s;
  overflow: hidden;
}

.pallete .info:hover {
  width: 100%;
  border-radius: 10px;
}
.pallete .info:hover .icon {
  display: none;
}

.pallete .info:hover .text {
  display: block;
}

.pallete .info .text {
  display: none;
  overflow: hidden;
}

.pallete {
  display: flex;
  flex-direction: row;
}
